
import { useContext } from 'react'
import DiscriptionHeaderOP from './DiscriptionHeaderOP'
import DiscriptionHeaderTM from './DiscriptionHeaderTM'
import CurcleDiagramm from '../../leftFrame/users/CurcleDiagramm'
import SpeedometrUser from '../../leftFrame/users/SpeedometrUser'
import { ContextStateDataGoggle } from '../../../context/context'
import useRouterType from '../../../hooks/hooksRoute'
import { GlobalDataTM, GlobalDataOP } from '../../../interfaces/interfaces'


import './Header.css'

const Header = () => {

    const data = useContext(ContextStateDataGoggle);
    const type = useRouterType()
    if (!data || !data.opStruktura || !data.tmStruktura) { // Проверяем все необходимые части данных
        return <div className="">Загрузка данных...</div>;
    }

    const globalData = type ? data.tmStruktura.globalData : data.opStruktura.globalData
    console.log(globalData?.factDay)
    return (
        <div className="container_header">
            {type ? <DiscriptionHeaderTM obj={globalData as GlobalDataTM} /> : <DiscriptionHeaderOP obj={globalData as GlobalDataOP} />}
            <CurcleDiagramm count={type ? globalData?.factMonth : globalData?.factDay} radius={60} strokeWidth={20} width={120} height={120} borderRadius={'5px'} />
            <SpeedometrUser count={type ? globalData?.factDay : globalData?.factMonth} radius={60} padding={15} strokeWidth={20} width={220} height={84} borderRadius={'5px'} />
        </div>
    )
}

export default Header