
import './SpeedometrUser.css'
import React, { useState, useEffect, useRef } from 'react'


interface PropsSpeedometrs {
    radius: number,
    strokeWidth: number,
    padding: number,
    width: number,
    height: number,
    borderRadius: string,
    count: string
}
const SpeedometrUser: React.FC<PropsSpeedometrs> = ({ count, radius, strokeWidth, padding, width, height, borderRadius }) => {

    console.log(count)
    const value = parseFloat(count.trim().slice(0, -1).replace(',', '.'))

    const halfCircumference = Math.PI * radius;
    const centerX = radius + strokeWidth / 2 + padding + 25;
    const centerY = radius + strokeWidth / 2 + padding;
    const dashOffset = halfCircumference - (value / 100) * halfCircumference;
    const innerRadius = radius - strokeWidth / 2;

    const needleAngleRad = (value / 100) * Math.PI;
    const needleX = centerX - radius * Math.cos(needleAngleRad);
    const needleY = centerY - radius * Math.sin(needleAngleRad);

    const riskPositions = [0, 25, 50, 75, 100];
    const riskAnglesRad = riskPositions.map((p) => {
        let angle = (p / 100) * Math.PI;
        if (p === 0) angle = 0.1;
        if (p === 100) angle = Math.PI - 0.1;
        return angle;
    });

    const riskCoordinates = riskAnglesRad.map((angle) => ({
        x1: centerX - innerRadius * Math.cos(angle),
        y1: centerY - innerRadius * Math.sin(angle),
        x2: centerX - radius * Math.cos(angle),
        y2: centerY - radius * Math.sin(angle),
        textX: centerX - (radius + 30) * Math.cos(angle),
        textY: centerY - (radius + 17) * Math.sin(angle),
    }));

    return (
        <div className='speedometr_user' style={{ borderRadius: borderRadius }}>
            <svg
                width={width}
                height={height}
            >
                <circle
                    cx={centerX}
                    cy={centerY}
                    r={radius}
                    fill="transparent"
                    stroke={value < 80 ? "red" : (value < 100 ? "yellow" : "green")}
                    strokeWidth={strokeWidth}
                />
                <circle
                    cx={centerX}
                    cy={centerY}
                    r={radius}
                    fill="transparent"
                    stroke="red"
                    strokeWidth={strokeWidth}
                    strokeDasharray={halfCircumference}
                    strokeDashoffset={dashOffset}
                />
                <line
                    x1={centerX}
                    y1={centerY}
                    x2={needleX}
                    y2={needleY}
                    stroke={value < 80 ? "red" : (value < 100 ? "yellow" : "green")}
                    strokeWidth="2"
                    strokeLinecap="round"
                />

                {riskCoordinates.map((coord, index) => (
                    <React.Fragment key={index + coord.x1}>
                        <line
                            key={`${index}-line`}
                            x1={coord.x1}
                            y1={coord.y1}
                            x2={coord.x2}
                            y2={coord.y2}
                            stroke="#fff"
                            strokeWidth="2"
                        />
                        <text
                            key={`${index}-text`}
                            x={coord.textX}
                            y={coord.textY}
                            textAnchor="middle"
                            dominantBaseline="middle"
                            fontSize="12px"
                            fill="#fff"
                        >
                            {riskPositions[index]}%
                        </text>
                    </React.Fragment>
                ))}

                <text
                    x={centerX + 5}
                    y={centerY - 10}
                    textAnchor="middle"
                    dominantBaseline="middle"
                    fill="#fff"
                    fontSize="16px"
                >
                    {value}%
                </text>
            </svg></div>
    );
};


export default SpeedometrUser