
import './CenterBody.css'
import React from 'react'
import { IoMdStar } from "react-icons/io";

import { useContext } from 'react'
import { ContextStateDataGoggle } from '../../context/context'
import useRouterType from '../../hooks/hooksRoute'

const CenterBody = () => {
    const data = useContext(ContextStateDataGoggle)
    const type = useRouterType()
    if (!data || !data.opStruktura || !data.tmStruktura) { // Проверяем все необходимые части данных
        return <div className="">Загрузка данных...</div>;
    }
    const managersData = type ? data.tmStruktura.managersData : data.opStruktura.managersData

    const card = managersData.map((user, index) => {
        if (index === 0) {
            return <React.Fragment key={index}>
                <div className='star_rate row'>{user.name} : {user.factMonth}<IoMdStar className="icon_star" /></div>
                <div className='image_manager row' style={{ backgroundColor: user.color }}>{user.name}</div>
            </React.Fragment>
        }
        else {
            return <div className='manager_rate row'>{user.name} : {user.factMonth}</div>
        }

    })
    return (
        <div className="container_body">
            <>
                <div className='title_rate row'>РЕЙТИНГ</div>
                {card}
            </>
        </div>
    )
}

export default CenterBody