
import './DiscriptionTM.css'
import React from 'react'
import { ManagersDataTM } from '../../../interfaces/interfaces'
interface ObjectPorps {
    obj: ManagersDataTM
}


const DiscriptionTM: React.FC<ObjectPorps> = ({ obj }) => {
    function truncateToThreeDecimals(num: number): number {
        return Math.floor(num * 1000) / 1000;
    }
    console.log(obj)
    return (
        <div className='wrap_disc_tm'>
            <div className="row_disc_tm">Звонки: <span className='value_disc'>x</span></div>
            <div className="row_disc_tm">Кол-во от 30 сек: <span className='value_disc'>x</span></div>
            <div className="row_disc_tm">Кол-во от 60 сек: <span className='value_disc'>x</span></div>
            <div className="row_disc_tm">Всего заявок: <span className='value_disc'>{obj.application}</span></div>
            <div className="row_disc_tm">Качественные заявки: <span className='value_disc'>{obj.qualityApplications}</span></div>

        </div>
    )
}


export default DiscriptionTM