
import './DiscriptionHeaderOP.css'
import React from 'react'
import { GlobalDataOP } from '../../../interfaces/interfaces'

interface ObjectProps {
    obj: GlobalDataOP
}

const DiscriptionHeaderOP: React.FC<ObjectProps> = ({ obj }) => {
    console.log(obj)
    function truncateToThreeDecimals(num: number): number {
        return Math.floor(num * 1000) / 1000;
    }
    return (
        <div className='discription_header'>
            <div className="row_disc_global">Рабочий день - <span className='value_disc'>{obj.jobDay}</span></div>
            <div className="row_disc_global">План месяца - <span className='value_disc'>{obj.planMonth}</span></div>
            <div className="row_disc_global">Факт месяца - <span className='value_disc'>{obj.factMonth}</span></div>
            <div className="row_disc_global">План на текущий день - <span className='value_disc'>{truncateToThreeDecimals(obj.planDay)}</span></div>
            <div className="row_disc_global">Факт на текущий день - <span className='value_disc'>{truncateToThreeDecimals(obj.factBalls)}</span></div>
        </div>
    )
}



export default DiscriptionHeaderOP