
import './DiscriptionOP.css'
import React from 'react'
import { ManagersDataOP } from '../../../interfaces/interfaces'
interface ObjectPorps {
    obj: ManagersDataOP
}


const DiscriptionOP: React.FC<ObjectPorps> = ({ obj }) => {
    function truncateToThreeDecimals(num: number): number {
        return Math.floor(num * 1000) / 1000;
    }
    return (
        <div className='wrap_disc'>
            <div className="row_disc">Звонки:<span className='value_disc'>x</span></div>
            <div className="row_disc">План на текущий день: <span className='value_disc'>{truncateToThreeDecimals(obj.planDay)}</span></div>
            <div className="row_disc">Факт на текущий день: <span className='value_disc'>{truncateToThreeDecimals(obj.factballs)}</span></div>
            <div className="row_disc">Месяц:  <span className='value_disc'>{obj.planMonth}</span></div>
        </div>
    )
}


export default DiscriptionOP